    import {  
        mapState,  
        mapMutations  
    } from 'vuex';  
    export default {  
		data() {
			return {
				storeInfo:[],
				sysnum:0,
				artlist:[]
			}				
		},
        computed: mapState(['sellerHasLogin']),  
		onLoad() {
			 console.log('页面加载');
			this.loadData();
		},
		onShow() {
             console.log('页面显示..');
         },
         onReady(){
             console.log('页面初次显示');
         },
         onHide() {
             console.log('页面隐藏');
         },
         onUnload() {
             console.log('页面卸载');
         },
         onBackPress(){
             console.log('页面返回...');
         },
         onShareAppMessage() {
             console.log('分享!');
         },
         onReachBottom() {
             console.log('下拉加载...');
         },
         onPageScroll(){
             console.log('页面滚动...');
         },
         onPullDownRefresh() {
             console.log('上拉刷新...');
             uni.stopPullDownRefresh();
         },
        methods: {   
            async loadData() {  
				var _this=this;
				console.log('===sellerHasLogin==='+this.sellerHasLogin);
				
                if (!this.sellerHasLogin) {  
					uni.navigateTo({
						url:'login',
					})
                }else{
					_this.$Request.post('seller_index').then(res => {
						if (res.code == 200) {
							console.log(res);
							//this.login(res.datas);
							_this.storeInfo = res.datas.store_info;
							_this.artlist = res.datas.artlist;
							_this.sysnum =res.datas.sysnum;
							if(res.datas.setbill.alipay==0){
								uni.showToast({
									icon: 'none',
									title:'请设置您的支付宝提现方式',
									duration:2000,
									complete:function(e){
										setTimeout(function () {
											uni.navigateTo({
												url:"tx/txway"
											})
										 }, 2000);
									}
								});
								return false;
							}else if(res.datas.setbill.wxpay==0){
								uni.showToast({
									icon: 'none',
									title:'请设置您的微信提现方式',
									duration:2000,
									complete:function(e){
										setTimeout(function () {
											uni.navigateTo({
												url:"tx/txway"
											})
										 }, 2000);
									}
								});
								return false;
							}
						}
						else{
							uni.showToast({
								icon: 'none',
								title:res.datas.error,
								duration:2000
							});
							uni.navigateTo({
								url:'login',
							});
						}
					}); 
				}
            },
			toPage: function(url) {
				uni.navigateTo({
					url:url,
				})
			}
			 
        }  
    }  
